
.App-title {
  font-size: 1.5em;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  min-width: 930px;
  cursor: default;
}

.App.dropper {
  cursor: url("assets/dropper.png") 6 28, auto;
}

.four-up {
  display: -ms-flexbox;
  display: flex;
}

.four-up>div {
  padding: 2px;
}

.box {
  width: 500px;
  height: 312px;
  border: 1px solid;
  overflow: hidden;
  margin-bottom: 5px;
}

.view-box {
  position: relative;
}

.view-selection-container {
  position: absolute;
  left: 6px;
  top: 3px;
  z-index: 1;
}


